/* Общие стили */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Arial', sans-serif;
}

body {
  background-color: #000000;
  color: #ffffff;
  line-height: 1.6;
  min-height: 100vh;
}

.app {
  display: flex;
  background-color: #000000;
  color: #ffffff;
  line-height: 1.6;
  min-height: 100vh;
}

/* Боковая панель */
.sidebar {
  width: 70px;
  background-color: #000000;
  display: flex;
  flex-direction: column;
  padding: 20px 0;
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  z-index: 1000;
  transition: all 0.3s ease;
  overflow: hidden;
  border-right: 1px solid #ffffff;
}

.sidebar:hover {
  width: 220px;
}

.sidebar-content {
  width: 220px;
}

.sidebar-icons {
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: flex-start;
  padding: 0 12px;
}

.icon-link {
  color: #ffffff;
  font-size: 1.4rem;
  text-decoration: none;
  transition: all 0.3s ease;
  padding: 12px 15px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  width: 100%;
  position: relative;
  white-space: nowrap;
  cursor: pointer;
}

.icon-link:hover {
  color: #000000;
  background-color: #ffffff;
  transform: translateX(5px);
}

.icon-text {
  font-size: 0.95rem;
  margin-left: 15px;
  opacity: 0;
  transform: translateX(-10px);
  transition: all 0.3s ease 0.1s;
  font-weight: 500;
  color: #ffffff;
}

.sidebar:hover .icon-text {
  opacity: 1;
  transform: translateX(0);
}

.icon-link:hover .icon-text {
  color: #000000;
}

/* Основной контент */
.main-content {
  flex: 1;
  margin-left: 70px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  transition: margin-left 0.3s ease;
  background-color: #000000;
}

.sidebar:hover ~ .main-content {
  margin-left: 220px;
}

/* Верхняя панель */
.top-header {
  background-color: #000000;
  padding: 15px 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #ffffff;
  position: sticky;
  top: 0;
  z-index: 100;
}

.search-container {
  flex: 1;
  max-width: 500px;
}

.search-box {
  position: relative;
  display: flex;
  align-items: center;
  background: #000000;
  border: 1px solid #ffffff;
  border-radius: 25px;
  padding: 8px 15px;
  transition: all 0.3s ease;
}

.search-box:focus-within {
  border-color: #ffffff;
  background: #1a1a1a;
}

.search-icon {
  color: #ffffff;
  font-size: 1rem;
  margin-right: 10px;
}

.search-input {
  flex: 1;
  border: none;
  outline: none;
  background: transparent;
  font-size: 0.95rem;
  color: #ffffff;
  padding: 5px 0;
}

.search-input::placeholder {
  color: #cccccc;
}

.site-name {
  font-size: 1.4rem;
  font-weight: 700;
  color: #ffffff;
  margin-left: 30px;
}

/* Контент страницы */
.page-content {
  flex: 1;
  padding: 40px 30px;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

.content-header {
  margin-bottom: 30px;
  text-align: left;
}

.section-title {
  font-size: 2.2rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 8px;
}

.section-subtitle {
  font-size: 1.1rem;
  color: #cccccc;
  font-weight: 400;
}

/* Сетка карточек - 3 колонки */
.tracks-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 25px;
  margin-top: 30px;
}

/* Карточки с фото сверху и текстом снизу */
.track-card {
  background: #000000;
  border: 1px solid #333;
  border-radius: 12px;
  overflow: hidden;
  transition: all 0.3s ease;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(255, 255, 255, 0.1);
  display: flex;
  flex-direction: column;
  height: 350px; /* Высота карточки */
}

.track-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(255, 255, 255, 0.2);
  border-color: #555;
}

/* Контейнер для фото сверху */
.track-image-container {
  background: linear-gradient(
    135deg,
    #1a1a1a 0%,
    #2a2a2a 50%,
    #1a1a1a 100%
  );
  overflow: hidden;
  position: relative;
  /* Высота фото - 70% карточки */
  height: 245px; /* 350px * 0.7 = 245px */
  flex-shrink: 0;
}

.track-image-background {
  width: 100%;
  height: 100%;
  background-size: cover;background-position: center;
  background-repeat: no-repeat;
}

/* Черный текстовый блок под фото */
.track-info-container {
  background: #000000;
  border-top: 1px solid #333;
  padding: 20px;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 105px; /* 350px * 0.3 = 105px */
}

.track-name {
  font-size: 1.3rem;
  font-weight: 700;
  color: #ffffff;
  display: block;
  margin-bottom: 8px;
  text-align: center;
}

.track-artist {
  font-size: 1rem;
  color: #bb86fc;
  display: block;
  font-weight: 500;
  text-align: center;
}

/* Адаптивность */
@media (max-width: 1024px) {
  .tracks-grid {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 20px;
  }
  
  .track-card {
    height: 320px;
  }
  
  .track-image-container {
    height: 224px; /* 320px * 0.7 */
  }
  
  .track-info-container {
    min-height: 96px; /* 320px * 0.3 */
    padding: 15px;
  }
}

@media (max-width: 768px) {
  .sidebar {
    width: 60px;
  }
  
  .sidebar:hover {
    width: 200px;
  }
  
  .main-content {
    margin-left: 60px;
  }
  
  .sidebar:hover ~ .main-content {
    margin-left: 200px;
  }
  
  .top-header {
    padding: 12px 20px;
    flex-direction: column;
    gap: 15px;
  }
  
  .search-container {
    max-width: 100%;
  }
  
  .site-name {
    margin-left: 0;
    font-size: 1.2rem;
  }
  
  .page-content {
    padding: 30px 20px;
  }
  
  .section-title {
    font-size: 1.8rem;
  }
  
  .section-subtitle {
    font-size: 1rem;
  }
  
  .tracks-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
  
  .track-card {
    height: 300px;
  }
  
  .track-image-container {
    height: 210px; /* 300px * 0.7 */
  }
  
  .track-info-container {
    min-height: 90px; /* 300px * 0.3 */
    padding: 12px;
  }
  
  .track-name {
    font-size: 1.1rem;
  }
  
  .track-artist {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .tracks-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .track-card {
    height: 280px;
  }
  
  .track-image-container {
    height: 196px; /* 280px * 0.7 */
  }
  
  .track-info-container {
    min-height: 84px; /* 280px * 0.3 */
    padding: 10px;
  }
  
  .track-name {
    font-size: 1rem;
  }
  
  .track-artist {
    font-size: 0.85rem;
  }
}
