/* Стили для страницы статьи */
.article-container {
  background: #000000;
  min-height: 100vh;
  padding: 40px;
  position: relative;
}

/* Кнопка назад */
.back-button {
  position: fixed;
  top: 30px;
  left: 30px;
  background: rgba(17, 17, 17, 0.9);
  border: 1px solid #333;
  color: #ffffff;
  padding: 12px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.4s ease;
  backdrop-filter: blur(10px);
  z-index: 1000;
  opacity: 0;
  transform: translateX(-20px);
}

.back-button.visible {
  opacity: 1;
  transform: translateX(0);
}

.back-button:hover {
  background: rgba(34, 34, 34, 0.95);
  border-color: #bb86fc;
  transform: translateX(0) scale(1.05);
}

/* Основной контент статьи */
.article-content {
  max-width: 800px;
  margin: 0 auto;
  opacity: 0;
  transform: translateY(50px);
  transition: all 0.8s ease;
}

.article-content.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Заголовок статьи */
.article-header {
  text-align: center;
  margin-bottom: 50px;
  padding-top: 20px;
}

.article-category {
  display: inline-block;
  background: #bb86fc;
  color: #000000;
  padding: 8px 20px;
  border-radius: 25px;
  font-size: 0.9rem;
  font-weight: 700;
  margin-bottom: 20px;
  letter-spacing: 0.5px;
}

.article-title {
  font-size: 3rem;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 20px;
  line-height: 1.2;
  background: linear-gradient(135deg, #ffffff 0%, #bb86fc 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.article-date {
  color: #bb86fc;
  font-size: 1.1rem;
  font-weight: 500;
}

/* Изображение статьи */
.article-image-container {
  margin-bottom: 50px;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 20px 40px rgba(187, 134, 252, 0.2);
  transform: scale(0.95);
  transition: all 0.6s ease 0.2s;
}

.article-content.visible .article-image-container {
  transform: scale(1);
}

.article-image {
  width: 100%;
  height: auto;
  display: block;
  transition: transform 0.6s ease;
}

.article-image-container:hover .article-image {
  transform: scale(1.05);
}

/* Текст статьи */
.article-text {
  line-height: 1.8;
  font-size: 1.2rem;
  color: #e0e0e0;
}

.article-text h2 {
  color: #ffffff;
  margin: 60px 0 25px 0;
  font-size: 2rem;
  font-weight: 700;
  border-left: 4px solid #bb86fc;
  padding-left: 20px;
}

.article-text p {
  margin-bottom: 25px;
  color: #cccccc;
  font-size: 1.1rem;
}

.article-text ul {
  margin: 30px 0;
  padding-left: 0;
}

.article-text li {
  margin-bottom: 15px;
  color: #cccccc;
  padding-left: 30px;
  position: relative;
  font-size: 1.1rem;
}

.article-text li:before {
  content: "▸";
  color: #bb86fc;
  position: absolute;
  left: 0;
  font-weight: bold;
}

.article-text blockquote {
  border-left: 4px solid #bb86fc;
  padding: 25px 30px;
  margin: 40px 0;
  font-style: italic;
  color: #bb86fc;
  background: rgba(187, 134, 252, 0.1);
  border-radius: 0 12px 12px 0;
  font-size: 1.3rem;
  line-height: 1.6;
}


/* Страница "не найдена" */
.article-not-found {
  text-align: center;
  padding: 100px 20px;
  color: #ffffff;
}

.article-not-found h2 {
  font-size: 2.5rem;
  margin-bottom: 30px;
  color: #bb86fc;
}

